@import "../_globalColor";

.dark-mode {
  background-color: $darkBackground;
  color: white;
  transition: "0.1s";
}

body, html {
  width: 100%;
  overflow-x: hidden; // ⬅️ prevents horizontal scroll
}

/* Add to Main.scss or your global layout file */
.section-container {
  height: auto; // ⬅️ was 100vh
  overflow: visible; // ⬅️ was hidden
  scroll-snap-align: unset;
}

.main {
  scroll-snap-type: none; // ⬅️ was y mandatory
  overflow: visible; // ⬅️ was scroll
  height: auto; // ⬅️ was 100vh
}
